	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Wednesday","date":"02 Feb 2000","slug":"/2000-02-02-wednesday/","tags":"Y2000"},"html":"<p>http://www.hislifeasaboy.com has a couple of new pictures</p>\n<p>Bier Verbot! The city has banned the Hamburg SV soccer club from selling strong beer at the stadium.... just before their match with league-leading Bayern Munich on Sunday. </p>\n<p><a href=\"http://www.internetworld.de/index_1592.html\">\"Webspace\" Trademark Revoked</a> (in German). German trademark owners who are not actively using their trademark can no longer claim damages from unsuspecting web sites for simply using the term. Even Microsoft was forced to pay for \"Explorer\".</p>\n<p>Our stock is up to 30 today... another 30% jump.</p>\n<p>I've been updating the list of <a href=\"http://shanson.editthispage.com/stories/storyReader$125\">German EditThisPage / Manila Sites</a> as I discover new sites.</p>"},"next":{"fields":{"title":"Inspired by the Readers","date":"01 Feb 2000","slug":"/2000-02-01-inspired-by-the-readers/"}},"previous":{"fields":{"title":"Thursday","date":"03 Feb 2000","slug":"/2000-02-03-thursday/"}}},{"node":{"fields":{"title":"Inspired by the Readers","date":"01 Feb 2000","slug":"/2000-02-01-inspired-by-the-readers/","tags":"Y2000"},"html":"<p>I have no idea why PapaScott has risen from 12th to 8th in the ETP <a href=\"http://www.editthispage.com/Top100\">Top 100</a>.</p>\n<p>I also have no idea why the stock of my employer has risen over 50% from 14 to 23 Euros the past 2 days. Our IPO in June was at 25, and we had been as low as 7.</p>"},"next":{"fields":{"title":"Back on Monday","date":"31 Jan 2000","slug":"/2000-01-31-back-on-monday/"}},"previous":{"fields":{"title":"Wednesday","date":"02 Feb 2000","slug":"/2000-02-02-wednesday/"}}},{"node":{"fields":{"title":"Back on Monday","date":"31 Jan 2000","slug":"/2000-01-31-back-on-monday/","tags":"Y2000"},"html":"<p>John Van Dyk: <a href=\"http://vfih.editthispage.com/2000/01/30\">Iowa in Winter</a></p>\n<p>Brent Simmons: <a href=\"http://inessential.com/linux/dellSetup\">Setting up a Dell Linux Box</a>. <i>Sounds like they took all the fun out of it ;-)</i></p>\n<p>I've started compiling a list of <a href=\"http://shanson.editthispage.com/stories/storyReader$125\">German EditThisPage / Manila Sites</a>. Are there any I don't know about yet?</p>\n<p>No, I didn't stay up until 4 am to watch the Super Bowl. My sleep was more important to me!</p>"},"next":{"fields":{"title":"Another Baby Hanson","date":"30 Jan 2000","slug":"/2000-01-30-another-baby-hanson/"}},"previous":{"fields":{"title":"Inspired by the Readers","date":"01 Feb 2000","slug":"/2000-02-01-inspired-by-the-readers/"}}}],"pathPrefix":"","first":false,"last":false,"index":1084,"pageCount":1101}}

